import cityDatas from '@/components/city.area.js'
import uniPopup from '@/components/uni-pop.vue'
export default {
	components: {
		uniPopup
	},
	data() {
		return {
			provinceData: cityDatas,
			cityData: [],
			areaData: [],
			selectList: [{
				txt: '请选择'
			}, {
				txt: '请选择'
			}, {
				txt: '请选择'
			}],
			tabOne: '请选择',
			indexTab: 0,
			proviceShow: true,
			areaShow: false,
			cityShow: false,
			show: false,
			type: '',
			checkOne: null,
			checkTwo: null,
			checkThree: null,
		}
	},
	onLoad() {

	},
	watch: {

	},
	methods: {
		togglePopup(type, open) {
			this.type = type
			if (open === 'tip') {
				this.show = true
			} else {
				this.$refs[open].open()
			}
		},
		cancel(type) {
			if (type === 'tip') {
				this.show = false
				return
			}
			this.$refs[type].close()
		},
		change(e) {
			if (e.show == true) {
				uni.hideTabBar()
			} else {
				uni.showTabBar()
			}
		},
		tabEvent(index) {
			this.indexTab = index
			if (this.indexTab == 0) {
				this.proviceShow = true
				this.cityShow = false
				this.areaShow = false
				// this.checkOne = null
				this.checkTwo = null
				this.checkThree = null
				// this.cityData = []
				this.areaData = []
				// this.selectList[0].txt = "请选择"
				this.selectList[1].txt = "请选择"
				this.selectList[2].txt = "请选择"
			} else if (this.indexTab == 1) {
				this.proviceShow = false
				this.cityShow = true
				this.areaShow = false
				// this.checkTwo = null
				this.checkThree = null
				// this.areaData = []
				// this.selectList[1].txt = "请选择"
				this.selectList[2].txt = "请选择"
			} else if (this.indexTab == 2) {
				this.proviceShow = false
				this.cityShow = false
				this.areaShow = true
			}
		},
		provinceEvent(data, index) {
			this.checkOne = index
			this.selectList[0].txt = data.name
			this.indexTab = 1
			this.proviceShow = false
			this.cityShow = true
			this.areaShow = false
			this.cityData = data.cityList
		},
		cityEvent(data, index) {
			this.checkTwo = index
			this.selectList[1].txt = data.name
			this.indexTab = 2
			this.proviceShow = false
			this.cityShow = false
			this.areaShow = true
			this.areaData = data.areaList
		},
		areaEvent(data, index) {
			this.checkThree = index
			this.selectList[2].txt = data.name
		}
	}
}
